* {
    margin: 0;
    padding:0;
    user-select: none;
}
@font-face {
    font-family: 'Akira';
    src: local('Akira'), url(./AkiraExpanded.otf) format('opentype');
}


#myVideo {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
  }

.logo {
    z-index: 999;
    width: 50vw;
    background-color: rgba(0,0,0,0.01);
    padding-bottom: 100px;
    min-width: 800px;
}

.mintBtn {
    cursor: pointer;
    border: none;
    margin-top: 20px;
    outline: 2px solid #fff;
    outline-offset: -2px;
    background: none;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
  font-family: "Akira";
  font-size: 36pt;
  letter-spacing: 5px;
  padding: 10px 100px 10px 100px;
  background: rgba(0,0,0,0.8);
  color: #fff;
  transition: 0.3s;
  min-width: 600px;
}
.mintBtn:hover {
    outline: 8px solid #fff;
    outline-offset: -8px;
    opacity: 0.6;
}


input[type=number] {
    border: none;
    width: 35px;
    text-align: center;
    font-family: "Akira";
    color: #fff;
    background-color: rgba(0,0,0,0);
    font-size: 24pt;
    outline: none;
}

input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.connectBtn {
    cursor: pointer;
    border: none;
    margin-top: 20px;
    outline: 2px solid #fff;
    outline-offset: -2px;
    background: none;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
  font-family: "Akira";
  font-size: 36pt;
  letter-spacing: 5px;
  padding: 10px 100px 10px 100px;
  background: rgba(0,0,0,0.8);
  color: #fff;
  transition: 0.3s;
  min-width: 600px;
}
.connectBtn:hover {
    outline: 8px solid #fff;
    outline-offset: -8px;
    opacity: 0.6;
}


.navBar {
    position: absolute;
    top:0;
    right: 0;
    padding: 20px;
    color: #fff;
}

.nftAmountPanel {
    display: flex;
    justify-content: center;
    font-size: 24pt;
    width: 100%;
    padding: 2vh;
}

.mintBtnLeft {
    height: 20px;
    width: 20px;
    padding: 10px;
    pointer-events: auto;
    cursor: pointer;
    text-decoration: none;
    user-select: none;
}
.mintBtnLeft:hover {
    opacity: 0.3;
}

.mintBtnRight {
    height: 20px;
    width: 20px;
    padding: 10px;
    cursor: pointer;
    text-decoration: none;
    user-select: none;
}
.nftAmountInput {
    font-size: 24pt;
    padding: 10px;
}
.mintBtnRight:hover {
    opacity: 0.3;
}

.navBar a {
    text-decoration: none;
    color: #fff;
}

.navBar a:hover {
    text-decoration: none;
    color: #fff;
    opacity:0.4;
}

.mainContainer {
    color: #fff;
    font-family: "Akira";
    font-size: 16pt;
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background-color: #000;
    background-size: 100% 100%;
}

.mintContainer {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}